import { _decorator, Component, Node, Sprite, SpriteFrame, resources, sp, spriteAssembler } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('pao')
export class pao extends Component {
    start() {
        this.node.on(Node.EventType.MOUSE_DOWN, (event) => {
            console.log('Mouse down');
            let node = new Node('infoBox');
            node.addComponent(Sprite);
            // node.getComponent(Sprite).spriteFrame = new SpriteFrame(url.raw("resources/game/wenzikuang"))
            node.parent = this.node;
            resources.load('wenzikuang',SpriteFrame,(err,spriteFrame)=>{
                node.getComponent(Sprite).spriteFrame = spriteFrame;
            })
            node.setPosition(0,0,-10);
          }, this);
    }

    update(deltaTime: number) {
        
    }
}


